import React from 'react';

function Card(props) {
    return (
        
<div className="card" style={{width: "20rem"}}>
<a href={props.link}>
  <img src={props.imgsrc} className="card-img-top" alt="subject-img"/>
  <div className="card-body">
    <h5 className="card-title">{props.title}</h5>
  </div>
</a>
</div>
        
    )
}

export default Card
