import React from 'react';
import Card from './Card';
import SS from "./images/ss.gif";
import "./Sem.css";
import STLD from "./images/stld.gif"
import FSDP from "./images/fsdp.gif"

function Fifth() {
    return (
        <div className="container cont1">
        <div className="row">
            <Card
            imgsrc = {SS}
            link ="./5thSem/"
            title = "System Software"
            />

<Card
            imgsrc = {STLD}
            link ="./5thSem/"
            title = "Switching Theory and Logic Design"
            />



<Card
            imgsrc = {FSDP}
            link ="./5thSem/"
            title = "File Structure and Data Processing"
            />




        </div>
        </div>
    )
}

export default Fifth
