import React from 'react';
import {NavLink} from "react-router-dom";


function Menu() {
    return (
        <div>
            
     <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div className="container-fluid">
    <NavLink className="navbar-brand" activeClassName="active" exact to="/my_cse/">My-Cse</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarNav">
      <ul className="navbar-nav">

        <li className="nav-item">
          <NavLink className="nav-link"  exact to="/my_cse/Fifth" >5th Sem</NavLink>
        </li>

        <li className="nav-item">
          <NavLink className="nav-link" exact  to="/my_cse/Sixth" >6th Sem</NavLink>
        </li>

        <li className="nav-item">
          <NavLink className="nav-link" exact to="/my_cse/Seventh"  >7th Sem</NavLink>
        </li>
        
      </ul>
    </div>
  </div>

</nav>
        </div>
    )
}

export default Menu
