import React from 'react';
import Card from './Card';
import DDA from "./images/dda.gif";
import DSP from "./images/dsp.gif";
import OOAD from "./images/ooad.gif";

function Seventh() {
    return (
        
        <div className="container cont1">
    <div className="row">
        <Card
        imgsrc = {DSP}
        link ="./7thSem/"
        title = "Digital Signal Processing"
        />

<Card
        imgsrc = {DDA}
        link ="./7thSem/"
        title = "Design and Analysis of Algorithm"
        />



<Card
        imgsrc = {OOAD}
        link ="./7thSem/"
        title = "Object Oriented Analysis & Design"
        />




    </div>
    </div>
    
    )
}

export default Seventh
